/**
 * Structure/layout
 */
.container {
  max-width: 57.445rem;
  margin: 0 auto;
  padding: 0 1rem;
  box-sizing: border-box;
  width: 100%;
}

@media only screen and (min-width: 500px) {
  .container {
    padding: 0 1rem;
  }
}

.grid--align-bottom .grid__col {
  vertical-align: bottom;
}

.grid__col {
  font-size: 1rem;
}

.page {
  display: flex;
  min-height: 100%;
  flex-direction: column;
  width: 100%;
  overflow-x: hidden;
}

.page__header {
  margin-bottom: 0;
}

.page__header:empty {
  display: none;
}

.page__subheader {
  padding: 1rem 0;
  overflow: hidden;
  min-height: 2.6rem;
}

.page__subheader:empty {
  height: 1rem;
}

.page__previous {
  float: left;
}

.has-nav-open .page__previous {
  display: none;
}

.page__previous--bottom {
  float: none;
}

.page__content {
  flex: 1 0 auto;
  padding-bottom: 1px;
  position: relative;
  overflow-x: hidden;
}

.page__menubtn {
  display: none;
  float: right;
}

.no-js .page__nav {
  margin-bottom: 2rem;
}

.has-js .page__nav {
  margin: 0 0 1rem;
  position: absolute;
  right: -15rem;
  width: 14rem;
  display: none;
  top: 0;
}

@media only screen and (min-width: 740px) {
  .has-js .page__nav {
    display: block;
    right: 0;
    position: relative;
    width: auto;
  }
}

.has-js .page__menubtn {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  line-height: 1;
}

.has-js .page__container {
  transition: transform 200ms ease-out;
}

.has-js .page__main {
  transform: translateX(0);
  transition: opacity 100ms ease-out;
}

.has-js .has-nav-open .page__container {
  transform: translateX(-15rem);
}

.has-js .has-nav-open .page__main {
  opacity: 0.2;
  pointer-events: none;
}

.has-js .has-nav-open .page__nav {
  display: block;
}

.page__prefooter {
  clear: both;
}

.page__footer {
  clear: both;
}

.jupiter {
  font-size: 1.75rem;
  font-weight: 600;
  line-height: 1.4;
}

@media only screen and (min-width: 740px) {
